import React from "react";
import { Redirect, Route, Switch } from "react-router-dom";

import Home from "./views/Home";
import Page from "./components/Page";
import NoPageFound from "./components/NoPageFound";
import Footer from "./views/Home/components/Footer";
import HeroSection from "./views/Home/components/HeroSection";
import BlockPass from "./views/BlockPass";
import Projects from "./views/Projects";

const Navigation = () => {
  return (
    <Switch>
      <Route exact path="/">
        <div>
          <HeroSection />
          <Page>
            <Home />
          </Page>
          <Footer />
        </div>
      </Route>
      <Route exact path="/projects/:id">
        <Page>
          <Projects />
        </Page>
      </Route>
      {/*<Route exact path="/projects/:id/kyc-verification">
        <Page>
          <BlockPass />
        </Page>
      </Route>*/}
      <Route exact path="/kyc">
        <Page>
          <BlockPass />
        </Page>
      </Route>
      <Route exact path="*">
        <NoPageFound />
      </Route>
      <Redirect to="/" />
    </Switch>
  );
}

export default Navigation;
