import React from "react";
import { Grid } from "@material-ui/core";

import TextWrapper from "../../components/TextWrapper";
import ProjectRowStatsCard from "../../components/ProjectRowStatsCard";

import { projects } from "../../config";
import ApplyForLaunch from "./components/ApplyForLaunch";
import ForwardRowStatsCard from "../Forward/components/ForwardRowStatsCard";

const Home = () => {
  return (
    <div className={"custom-container"}>
      <section>
        <div className="single-line-center-between m-b-40 mo-custom-container" id={"#pools"}>
          <TextWrapper
            text={"Projects"}
            fontSize={32}
            fontWeight={"bold"}
            lineHeight={'44px'}
          />
        </div>
        <Grid container spacing={2}>
          {Object.keys(projects).map((project: string, i: number) => {
            if (projects[project] === 'FORWARD') {
              return (
                <Grid item key={`${i}:${project}:${Date.now()}`} lg={12} md={12} sm={12} xs={12}>
                  <ForwardRowStatsCard tokenSymbol={projects[project]} id={project} />
                </Grid>
              )
            } else if (projects[project] === 'SCLP') {
              return (
                <Grid item key={`${i}:${project}:${Date.now()}`} lg={12} md={12} sm={12} xs={12}>
                  <ProjectRowStatsCard tokenSymbol={projects[project]} id={project} />
                </Grid>
              )
            } else return null
          })}
        </Grid>
        <ApplyForLaunch/>
      </section>
    </div>
  );
}

export default Home;
