import { BigNumber, Contract, ethers, Overrides } from 'ethers';

import ERC20 from './ERC20';
import ABIS from './deployments/abi';
import { Configuration } from '../utils/interface';
import { getDefaultProvider } from '../utils/provider';

/**
 * An API module of ARTH contracts.
 * All contract-interacting domain logic should be defined in here.
 */
export class Protocol {
  myAccount!: string;
  signer?: ethers.Signer;

  config: Configuration;
  contracts: { [name: string]: Contract };
  provider: ethers.providers.BaseProvider;

  SCLP: ERC20;
  DAI: ERC20;
  MAHA: ERC20;
  BUSD: ERC20;
  FORWARD: ERC20;

  tokens: {
    [name: string]: ERC20;
  };

  constructor(cfg: Configuration) {
    const { deployments } = cfg;
    const provider = getDefaultProvider(cfg);

    this.contracts = {};
    for (const [name, deployment] of Object.entries(deployments)) {
      if (!deployment.abi) continue;
      this.contracts[name] = new Contract(deployment.address, ABIS[deployment.abi], provider);
    }

    this.SCLP = new ERC20(deployments.SCLP.address, provider, 'SCLP', 18);
    this.DAI = new ERC20(deployments.DAI.address, provider, 'DAI', 18);
    this.MAHA = new ERC20(deployments.DAI.address, provider, 'MAHA', 18);
    this.BUSD = new ERC20(deployments.BUSD.address, provider, 'BUSD', 18);
    this.FORWARD = new ERC20(deployments.FORWARD.address, provider, 'FORWARD', 18);

    this.tokens = {
      SCLP: this.SCLP,
      DAI: this.DAI,
      MAHA: this.MAHA,
      BUSD: this.BUSD,
      FORWARD: this.FORWARD
    };

    this.config = cfg;
    this.provider = provider;
  };

  /**
   * @param provider From an unlocked wallet. (e.g. Metamask)
   * @param account An address of unlocked wallet account.
   */
  unlockWallet(provider: any, account: string) {
    const newProvider = new ethers.providers.Web3Provider(provider, this.config.chainId);

    this.signer = newProvider.getSigner(0);
    this.myAccount = account;
    for (const [name, contract] of Object.entries(this.contracts)) {
      this.contracts[name] = contract.connect(this.signer);
    }

    const tokens = [
      this.SCLP,
      this.DAI,
      this.MAHA,
      this.BUSD,
      this.FORWARD
    ];

    for (const token of tokens) {
      if (token && token.address) token.connect(this.signer);
    }
  };

  get isUnlocked(): boolean {
    return !!this.myAccount;
  };

  getTokenSaleSwapContract(token: string, id: string): Contract {
    return this.contracts[`${token}${id}FixedSwap`];
  }

  gasOptions(gas: BigNumber = BigNumber.from('6000000')): Overrides {
    const multiplied = Math.floor(gas.toNumber() * this.config.gasLimitMultiplier);
    return {
      gasLimit: BigNumber.from(multiplied),
    };
  };
}
